<template>
  <div class="mod">
    <el-collapse v-model="activeCollapse">
      <el-collapse-item>
        <template slot="title">
          &nbsp;&nbsp;
          <h2>Modpack</h2>
          &nbsp;
          <span>by</span>
          &nbsp;
          <h3>Anjo2</h3>
        </template>
        <div v-for="modpack in modpacks" :key="modpack.objectId">
          <el-card>
            <el-link :href="modpack.Url">下载</el-link>
            &nbsp;&nbsp;&nbsp;&nbsp;
            <a class="build">{{ modpack.Build }}</a>
            &nbsp;
            {{ modpack.TargetGameVersion.join("/") }}
          </el-card>
        </div>
      </el-collapse-item>
      <el-collapse-item>
        <template slot="title">
          &nbsp;&nbsp;
          <h2>LevelLoader</h2>
          &nbsp;
          <span>by</span>
          &nbsp;
          <h3>Anjo2</h3>
        </template>
        <div v-for="levelLoader in levelLoaders" :key="levelLoader.objectId">
          <el-card>
            <el-link :href="levelLoader.Url">下载</el-link>
            &nbsp;&nbsp;&nbsp;&nbsp;
            <a class="build">{{ levelLoader.Build }}</a>
            &nbsp;
            {{ levelLoader.TargetGameVersion.join("/") }}
          </el-card>
        </div>
      </el-collapse-item>
      <el-collapse-item>
        <template slot="title">
          &nbsp;&nbsp;
          <h2>ModpackAndLevelLoader</h2>
          &nbsp;
          <span>by</span>
          &nbsp;
          <h3>Nameless & Jor02</h3>
        </template>
        <div
          v-for="modpackAndLevelLoader in modpackAndLevelLoaders"
          :key="modpackAndLevelLoader.objectId"
        >
          <el-card>
            <el-link :href="modpackAndLevelLoader.Url">下载</el-link>
            &nbsp;&nbsp;&nbsp;&nbsp;
            <a class="build">{{ modpackAndLevelLoader.Build }}</a>
            &nbsp;
            {{ modpackAndLevelLoader.TargetGameVersion.join("/") }}
          </el-card>
        </div>
      </el-collapse-item>
      <el-collapse-item>
        <template slot="title">
          &nbsp;&nbsp;
          <h2>其他Mod</h2>
        </template>
        <div v-for="otherMod in otherMods" :key="otherMod.objectId">
          <el-card>
            <el-link :href="otherMod.Url">下载</el-link>
            &nbsp;&nbsp;&nbsp;&nbsp;
            <span class="name">{{ otherMod.Name }}</span>
            &nbsp;
            <span class="build">{{ otherMod.Build }}</span>
            &nbsp;
            <span>by</span>
            &nbsp;
            <span class="author">{{ otherMod.Author }}</span>
            &nbsp;
            <span>{{ otherMod.TargetGameVersion.join("/") }}</span>
          </el-card>
        </div>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeCollapse: [],
      modpacks: [],
      levelLoaders: [],
      modpackAndLevelLoaders: [],
      otherMods: [],
    };
  },
  mounted() {
    this.getMods();
  },
  methods: {
    async getMods() {
      const headers = new Headers({
        "X-LC-Id": "3Dec7Zyj4zLNDU0XukGcAYEk-gzGzoHsz",
        "X-LC-Key": "uHF3AdKD4i3RqZB7w1APiFRF",
        "Content-Type": "application/json",
      });
      const options = {
        method: "GET",
        headers: headers,
      };
      let params = {
        order: "-Build",
        //keys: "Name,Author,Platform,Url,Size,Form,Style,Difficulty,Preview,-createdAt,-objectId",
        //where: JSON.stringify(where),
      };
      const url1 = `https://3dec7zyj.lc-cn-n1-shared.com/1.1/classes/Modpack?${new URLSearchParams(
        params
      )}`;
      const url2 = `https://3dec7zyj.lc-cn-n1-shared.com/1.1/classes/LevelLoader?${new URLSearchParams(
        params
      )}`;
      const url3 = `https://3dec7zyj.lc-cn-n1-shared.com/1.1/classes/ModpackandLevelLoader?${new URLSearchParams(
        params
      )}`;
      const url4 = `https://3dec7zyj.lc-cn-n1-shared.com/1.1/classes/OtherMod?${new URLSearchParams(
        params
      )}`;
      const response1 = await fetch(url1, options);
      this.modpacks = (await response1.json()).results;
      const response2 = await fetch(url2, options);
      this.levelLoaders = (await response2.json()).results;
      const response3 = await fetch(url3, options);
      this.modpackAndLevelLoaders = (await response3.json()).results;
      const response4 = await fetch(url4, options);
      this.otherMods = (await response4.json()).results;
    },
  },
};
</script>

<style scoped>
.name {
  font-size: 20px;
}
.build {
  font-weight: 100;
  font-size: 18px;
}
.author {
  font-weight: 200;
  font-size: 18px;
}
</style>